html {
  box-sizing: border-box;
  height: 100%;
  font-family: Alegreya, serif;
  font-size: 100%;
}

*, :before, :after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

h1 {
  text-align: left;
  font-size: 3rem;
}

h1 + p {
  text-align: left;
  font-size: 2rem;
}

h2 {
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 1.5rem;
}

p {
  margin-bottom: 1em;
  font-size: 1rem;
  line-height: 1.6;
}

.p-footer {
  margin-bottom: 0;
  font-size: 1rem;
  line-height: 1.6;
}

.texto-foto {
  text-shadow: -1px -1px #000, 1px -1px #000, -1px 1px #000, 1px 1px #000;
}

body {
  text-align: left;
  background-color: #f2f2e6;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  display: flex;
}

footer {
  color: #fff;
  background-color: #000;
  font-size: 1.2rem;
}

.flex-container {
  flex-direction: column;
  justify-content: center;
  padding: 1rem 3rem;
  display: flex;
}

.text-container {
  text-align: center;
  padding-left: 3rem;
  padding-right: 3rem;
}

.full-img-container {
  color: #f5f5f7;
  background-position: center;
  background-size: cover;
  justify-content: left;
  align-items: center;
  height: 50vh;
  transition: background-image 1s ease-in-out;
  display: flex;
  position: relative;
}

.grid, .grid-invertido {
  grid-template-columns: 2fr 1fr;
  align-items: center;
  gap: 1rem;
  height: auto;
  margin-bottom: 2rem;
  display: grid;
}

.texto {
  text-align: justify;
}

.imagen img {
  -o-object-fit: cover;
  object-fit: cover;
  max-width: 100%;
  height: auto;
}

.grid-invertido {
  grid-template-columns: 1fr 2fr;
}

.imagen-pasar-container {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.flechas {
  color: #fff;
  cursor: pointer;
  background-color: #00000080;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  padding: .5rem;
  font-size: 1rem;
  position: absolute;
}

.flechas:hover {
  color: #000;
  background-color: #ffffff80;
}

#btn-prev, #btn-prev-menu, #btn-prev-chef {
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}

#btn-next, #btn-next-menu, #btn-next-chef {
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

#imagen_quienes, #imagen_menu, #imagen_chef {
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: auto;
}

.saber-mas {
  color: #fff;
  cursor: pointer;
  background-color: #dc1414;
  border: none;
  border-radius: 5px;
  width: 20%;
  height: 40px;
  margin-top: 1rem;
  padding: .5rem 1rem;
  font-size: 1rem;
}

.saber-mas:hover {
  background-color: #a30000;
}

.ul-nav {
  text-transform: uppercase;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.ul-nav li {
  margin-left: 20px;
}

.ul-nav .active a {
  color: #dc1414;
  font-weight: bold;
}

.ul-nav a {
  color: #000;
  font-weight: bold;
  text-decoration: none;
}

.ul-nav li a:hover {
  color: #dc1414;
}

.dos-columnas {
  text-align: center;
  grid-template-columns: 1fr 1fr;
  align-items: flex-start;
  gap: 1rem;
  display: grid;
}

.tres-columnas {
  text-align: center;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: flex-start;
  gap: 1rem;
  display: grid;
}
